<template>
  <div>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
       <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
      offset="50"
    >
      <ArticleItem
        v-for="obj in articlesList"
        :key="obj.art_id"
        :artObj="obj"
      ></ArticleItem>
    </van-list>
    </van-pull-refresh>

  </div>
</template>

<script>
// 问题：created和onload中的getArticlesAPI函数页面初始化都会执行一次
// 原因：van-list 页面初始化会立即执行，immediate-check默认会true
// 解决1：将van-list上的immediate-check设置为false 此方法存在问题；下拉之后再切换还是会执行2次
// 解决2：在onload第一行判断articlesList是否有数据，无数据就return

import ArticleItem from './ArticleItem.vue'
import { getArticlesAPI } from '@/api/index'
export default {
  props: {
    channelId: Number
  },
  data () {
    return {
      articlesList: [],
      loading: false,
      finished: false,
      timestamp: new Date().getTime(),
      isLoading: false
    }
  },
  created () {
    // console.log(111)
    this.getArticlesListFn()
  },
  components: {
    ArticleItem
  },
  methods: {
    async getArticlesListFn () {
      // 文章列表
      const res = await getArticlesAPI(this.channelId, this.timestamp)
      // console.log(res)
      // 数组合并
      this.articlesList.push.apply(this.articlesList, res.data.data.results)
      this.timestamp = res.data.data.pre_timestamp

      this.loading = false
      // 判断返回的上一页的时间戳是否为null，为null则代表无下一页 finished改为true
      if (this.timestamp === null) {
        this.finished = true
      }
    },
    onLoad () {
      if (this.articlesList.length === 0) return
      // console.log(222)
      this.getArticlesListFn()
    },
    onRefresh () {
      this.articlesList = []
      this.timestamp = new Date().getTime()
      this.getArticlesListFn()
      // 顶部加载状态
      this.isLoading = false
    }
  }
}
</script>

