<template>
  <div>
    <div>
      <!--黑马头条 顶部 -->
      <van-nav-bar fixed>
        <template #left>
          <img src="@/assets/toutiao_logo.png" class="logo" />
        </template>
        <template #right>
          <van-icon name="search" size="0.48rem" color="#fff"
            @click="$router.push({path:'/search'})" />
        </template>
      </van-nav-bar>
    </div>
    <!-- 频道列表 -->
    <div class="main">
      <van-tabs v-model="channelId" animated sticky offset-top="1.2266667rem"
        @change="channelChangeFn">
        <van-tab :name="obj.id" :title="obj.name"
          v-for="obj in userChannelsList" :key="obj.id">
          <ArticleList :channelId="channelId"></ArticleList>
        </van-tab>
      </van-tabs>
    </div>

    <!-- 编辑频道图标 -->
    <van-icon name="plus" size="0.37333334rem" class="moreChannels"
      @click="showPopup" />

    <!-- 频道管理弹出层 -->
    <van-popup v-model="show" position="left">
      <ChannelEdit :userList="userChannelsList" :moreList="unCheckChannelList"
        @addChannelEV="addChannelFn" @delChannelEV="delChannelFn"
        @closeEV="show=false" @changeChannelEV="changeChannelFn">
      </ChannelEdit>
    </van-popup>

  </div>
</template>

<script>
import { getUserChannelsAPI, getAllChannelsAPI, updateUserChannels, delUserChannels } from '@/api/index'
import ArticleList from './components/ArticleList.vue'
import ChannelEdit from './ChannelEdit.vue'
export default {
  data () {
    return {
      channelId: 0,
      userChannelsList: [],
      alluserChannelsList: [],
      show: false,
      channelScrollTopObj: {}
    }
  },
  async created () {
    // 频道列表
    const res = await getUserChannelsAPI()
    // console.log(res)
    this.userChannelsList = res.data.data.channels
    // this.channelChangeFn()
    // 所有频道列表
    const res2 = await getAllChannelsAPI()
    console.log(res2)
    this.alluserChannelsList = res2.data.data.channels
  },
  computed: {
    unCheckChannelList () {
      // 目的：把所有的频道数组挨个取出，然后将拿到的结果去用户数组查找，如果没有，则存入新数组
      // const newArr = this.alluserChannelsList.filter((bigObj) => {
      //   const index = this.userChannelsList.findIndex((obj) => obj.id === bigObj.id)
      //   如果找到了
      // if (index > -1) {
      //     return false
      //   } else {
      //     return true
      //   }
      // })
      // return newArr
      // 简写
      return this.alluserChannelsList.filter(
        (bigObj) =>
          this.userChannelsList.findIndex((smallObj) => smallObj.id === bigObj.id) === -1
      )
    }
  },
  components: {
    ArticleList,
    ChannelEdit
  },
  methods: {
    showPopup () {
      this.show = true
    },
    // 切换频道
    channelChangeFn () {
      // console.log('切换了')
      // tab切换时，会将原来的标签height设置为0，所以切回去的时候滚动条无高度就在顶部了
      // 解决：延迟执行 nextTick在dom更新完成之后执行
      this.$nextTick(() => {
        document.documentElement.scrollTop = this.channelScrollTopObj[this.channelId]
      })
    },

    // 添加频道
    async addChannelFn (channelObj) {
      // console.log(channelObj)
      this.userChannelsList.push(channelObj)
      await updateUserChannels(this.userChannelsList)
      // console.log(res)
    },
    // 删除频道
    async delChannelFn (channelObj) {
      // console.log(channelObj.id)
      this.userChannelsList = this.userChannelsList.filter(obj => {
        return obj.id !== channelObj.id
      })
      await delUserChannels(channelObj.id)

      // if (channelObj.id !== 0) {
      //   this.userChannelsList = this.userChannelsList.filter(obj => {
      //     return obj.id !== channelObj.id
      //   })
      //   await delUserChannels(channelObj.id)
      // }
    },
    // 修改频道
    changeChannelFn (id) {
      this.channelId = id
      this.show = false
    },

    scrollFn () {
      this.$route.meta.scrollT = document.documentElement.scrollTop
      // 保存当前频道的滚动距离
      this.channelScrollTopObj[this.channelId] = document.documentElement.scrollTop
      // console.log(this.channelScrollTopObj)
    }
  },
  // 获取焦点时执行 前提是使用了组件缓存 keep-alive
  activated () {
    document.documentElement.scrollTop = this.$route.meta.scrollT
    // 监听网页滚动事件 实时保存高度
    window.addEventListener('scroll', this.scrollFn)
    // console.log(this.$route)
  },
  // 失去焦点时移除监听事件
  deactivated () {
    window.removeEventListener('scroll', this.scrollFn)
  }

}
</script>

<style scoped lang="less">
.logo {
  width: 100px;
  height: 30px;
}
.main {
  padding-top: 46px;
}

/* 设置 tabs 容器的样式 */
/deep/ .van-tabs__wrap {
  padding-right: 30px;
  background-color: #fff;
}

/* 设置小图标的样式 */
.moreChannels {
  position: fixed;
  top: 62px;
  right: 8px;
  z-index: 999;
}
.van-popup {
  width: 100vw;
  height: 100vh;
}
</style>
