<template>
  <div>
    <!-- 一条文章单元格 -->
    <van-cell @click="goDetailFn(artObj.art_id)">
      <!-- 标题区域的插槽 -->
      <template #title>
        <div class="title-box">
          <!-- 标题 -->
          <span>{{ artObj.title }}</span>
          <!-- v-lazy="artObj.cover.images[0]" 图片懒加载 -->
          <!-- 单图 -->
          <img class="thumb" v-if="artObj.cover.type === 1"
            v-lazy="artObj.cover.images[0]" />
        </div>
        <!-- 三图 -->
        <div class="thumb-box" v-if="artObj.cover.type > 1">
          <img class="thumb" v-for="(item, index) in artObj.cover.images"
            :key="index" v-lazy="item" />
        </div>
      </template>
      <!-- label 区域的插槽 -->
      <template #label>
        <div class="label-box">
          <div>
            <span>{{ artObj.aut_name }}</span>
            <span>{{ artObj.comm_count }}评论</span>
            <span>{{ formatTime(artObj.pubdate) }}</span>
          </div>
          <!-- 反馈按钮 -->
          <van-icon name="cross" @click.stop="show = true" />
        </div>
      </template>
    </van-cell>

    <!-- 反馈面板 -->
    <van-action-sheet v-model="show" :actions="actions"
      :cancel-text="cancelText" close-on-click-action @cancel="onCancel"
      @select="onSelect" @close="closeFn" get-container="body" />
  </div>
</template>

<script>
import Notify from '@/ui/Notify.js'
import { timeAgo } from '@/utils/date.js'
import { firstActions, secondActions } from '@/api/report.js'
import { articleDislike, articleReports } from '@/api/index.js'

export default {
  data () {
    return {
      show: false,
      actions: firstActions,
      cancelText: '取消',
      remark: ''
    }
  },
  props: {
    artObj: Object
  },
  methods: {
    formatTime: timeAgo,
    onCancel () {
      // 点击返回按钮，反馈面板内容为一级内容，按钮文字改为取消，面板继续显示
      if (this.cancelText === '返回') {
        this.actions = firstActions
        this.cancelText = '取消'
        this.show = true
      }
    },
    closeFn () {
      // 遮罩层关闭 反馈面板内容为一级内容，按钮文字改为取消
      this.actions = firstActions
      this.cancelText = '取消'
    },
    async onSelect (action, index) {
      // console.log(action)
      // console.log(index)
      if (action.name === '反馈垃圾内容') {
        console.log(secondActions)
        this.actions = secondActions
        this.show = true
        this.cancelText = '返回'
      } else if (action.name === '不感兴趣') {
        await articleDislike(this.artObj.art_id) // 点击不感兴趣 调用接口
        Notify({ type: 'success', message: '反馈成功' })
      } else {
        // 点击二级反馈的选项 调用接口
        if (secondActions[index].value === 0) {
          // 判断是否是其他问题，如果是 传入具体问题内容
          this.remark = '测试其他问题'
        }
        try {
          await articleReports(
            this.artObj.art_id,
            secondActions[index].value,
            this.remark
          )
          Notify({ type: 'success', message: '举报成功' })
        } catch (error) {
          // Notify({ type: 'danger', message: '举报失败' })
        }
      }
    },
    // 跳转文章详情界面
    goDetailFn (artId) {
      // console.log(artId)
      this.$router.push({
        path: '/article_detail',
        query: { aid: artId }
      })
    }
  }
}
</script>

<style scoped lang="less">
/* 标题样式 */
.title-box {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

/* label描述样式 */
.label-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* 文章信息span */
.label-box span {
  margin: 0 3px;
  &:first-child {
    margin-left: 0;
  }
}
/* 图片的样式, 矩形黄金比例：0.618 */
.thumb {
  width: 113px;
  height: 70px;
  background-color: #f8f8f8;
  object-fit: cover;
}

/* 三图, 图片容器 */
.thumb-box {
  display: flex;
  justify-content: space-between;
}
</style>
